import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import router from './router/index'
import store from './store/index';
Vue.config.productionTip = false

new Vue({
  vuetify,
  router,
  store,
  created() {
    this.$store.dispatch("listenRx")
  },
  render: h => h(App)
}).$mount('#app')
